.form {
    p {
        font-size: 1.4rem;
        margin: 2rem 0 0 0;
        color: $darkGrey;
    }

    legend {
        font-size: 2rem;
        color: $darkGrey;
    }

    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }

    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: white;
        border: 1px solid $grey;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    textarea{
        height: 10rem;
    }
    .contact__method{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
}