/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content__centered {
  max-width: 80rem;
}

.align__right {
  display: flex;
  justify-content: flex-end;
}

.button__yellow--block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.button__yellow--block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.button__yellow {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.button__yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.button__green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.button__green:hover {
  background-color: #619800;
  cursor: pointer;
}

.darkMode {
  background-color: #1a1a1a;
}
.darkMode p, .darkMode blockquote {
  color: white;
}
.darkMode a, .darkMode h1, .darkMode h2, .darkMode h3, .darkMode h4 {
  color: #e1e1e1;
}
.darkMode .announcement {
  background-color: #333333;
  border: 1px solid #333333;
}
.darkMode .announcement .price {
  color: #71B100;
}
.darkMode .announcement .icon {
  filter: invert(100%);
}
.darkMode .property__summary .iconA {
  filter: invert(100%);
}
.darkMode .property__summary .price {
  color: #71B100;
}
.darkMode .form {
  color: white;
}
.darkMode .form fieldset legend, .darkMode .form fieldset p {
  color: white;
}
.darkMode .form input:not([type=submit]),
.darkMode .form textarea,
.darkMode .form select {
  background-color: #333333;
  border: 1px solid #333333;
}

/*------ UI -------*/
.no-webp .header__start {
  background-image: url(../img/header.jpg);
}

.webp .header__start {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.header__start {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.header__start {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.header__start .header__content {
    min-height: 70rem;
  }
}

.header__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem;
}
.footer .navigation {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .footer .navigation {
    flex-direction: row;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
.right .dark__mode--button {
  width: 3rem;
  margin-top: 5rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.right .dark__mode--button:hover {
  cursor: pointer;
}

.container__bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .container__bar {
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.navigation {
  display: none;
  visibility: hidden;
  opacity: 0;
}
@media (min-width: 768px) {
  .navigation {
    visibility: visible;
    opacity: 1;
    display: flex;
    align-items: center;
  }
}
.navigation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
.navigation a:hover {
  color: #71B100;
  transition: ease 0.2s all;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
  }
  .navigation a:last-of-type {
    margin-right: 0;
  }
}

/*----hamburger menu----*/
@media (min-width: 768px) {
  .mobile__menu {
    display: none;
  }
}
.mobile__menu img {
  width: 3.5rem;
}

.container__bh {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.navigation__mobile {
  display: none;
  visibility: hidden;
  opacity: 0;
  flex-direction: column;
  height: 0;
  width: 100%;
  border: 1px solid #e1e1e1;
  border-radius: 3rem;
  justify-content: center;
  align-items: center;
  transition: visibility 0.4s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navigation__mobile {
    display: none;
  }
}

.navigation__mobile--modifier {
  display: flex;
  flex-direction: column;
}
.navigation__mobile--modifier a {
  color: white;
  font-size: 2rem;
}
.navigation__mobile--modifier a:hover {
  color: #71B100;
  transition: ease 0.2s all;
}

.show {
  display: flex;
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .us__icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .container__announcements {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.announcement {
  background-color: #eeeeee;
  border: 1px solid #d4d4d4;
  margin-bottom: 2rem;
}
.announcement .content__announcement {
  padding: 2rem;
}
.announcement h3, .announcement p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons__characteristics {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.icons__characteristics li {
  flex: 1;
  display: flex;
}
.icons__characteristics li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons__characteristics li p {
  font-weight: 900;
}

.no-webp .image__contact {
  background-image: url("../img/encuentra.jpg");
}

.webp .image__contact {
  background-image: url("../img/encuentra.webp");
}

.image__contact {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.image__contact h2 {
  font-size: 4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.image__contact p {
  color: white;
}

@media (min-width: 768px) {
  .inferior__section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entry__blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entry__blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entry__blog:last-of-type {
  margin-bottom: 0;
}

.meta__info span {
  color: #E08709;
}

.text__entry a {
  color: black;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .text__entry a {
    margin-top: 0;
  }
}
.text__entry h4 {
  margin: 0;
  line-height: 1.4;
}
.text__entry h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: white;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  height: 8rem;
  width: 6rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
}

.form p {
  font-size: 1.4rem;
  margin: 2rem 0 0 0;
  color: #333333;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: white;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 10rem;
}
.form .contact__method {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/*------- Internal pages -------*/
@media (min-width: 768px) {
  .about__content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.text__about blockquote {
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}